table {
    border-collapse: collapse;
    width: 100%;
}

th,
td {
    padding: 6px;
    text-align: center;
    border: 1px solid #ddd;
}

th {
    background-color: #f2f2f2;
    font-weight: bold;
}

td {
    font-weight: bold;
}

th.saturday {
    color: blue;
}

th.sunday {

    color: red;
}